<template>
    <div class="area-div">
        <jiedanyuan_ysdsdd :wrapHeight="wrapHeight" ref="jiedanyuan_ysdsdd"></jiedanyuan_ysdsdd>
    </div>
</template>
<script>
    export default {
        components: {
            'jiedanyuan_ysdsdd':resolve => require(['./jiedanyuan_ysdsdd'], resolve),
        },
        data(){
            return{
                wrapHeight:420
            }
        },
        mounted() {
            this.init();
        },

        methods: {
            init() {
               this.getlist();
            },

            //获取数据
            getlist(){},
        }
    }
</script>
<style scoped lang="less" type="text/less">
    .area-div{
        height: 100%;
    }
</style>




